.form-control:focus {
  color: #495057;
  background-color: #fff;
  border-color: #495057bd;
  outline: 0;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0);
}
input[type="text"]::placeholder {
  color: #000000de;
  font-size: 14px;
}
.form-control-submit {
  border: 1px solid #8f8fa1;
  border-radius: 4px;
  padding: 10px 16px;
  height: 100%;
}
label.custom-checkbox input,
label.custom-radio input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
label.custom-checkbox,
label.custom-radio {
  display: block;
  position: relative;
  padding-left: 25px;
  margin-bottom: 10px;
  cursor: pointer;
  user-select: none;
  line-height: normal;
  font-size: 14px;
}
label.custom-checkbox:last-child,
label.custom-radio:last-child {
  margin-bottom: 10px;
}
/* Create a custom checkbox */

label.custom-checkbox .checkmark,
label.custom-radio .radio {
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid #d4d4db;
  width: 20px;
  height: 20px;
  background-color: #fff;
  border-radius: 4px;
}
/* On mouse-over, add a grey background color */

label.custom-checkbox:hover input ~ .checkmark,
label.custom-radio input:checked ~ .radio {
  background-color: #fff;
  border: 1px solid #13130d;
}
/* When the checkbox is checked, add a blue background */

label.custom-checkbox input:checked ~ .checkmark,
label.custom-radio input:checked ~ .radio {
  background-color: #13130d;
  border: 1px solid #13130d;
}
/* Create the checkmark/indicator (hidden when not checked) */

.checkmark:after,
.radio:after {
  content: "";
  position: absolute;
  display: none;
}
/* Show the checkmark when checked */

label.custom-checkbox input:checked ~ .checkmark:after,
label.custom-radio input:checked ~ .radio:after {
  display: block;
}
/* Style the checkmark/indicator */

label.custom-checkbox .checkmark:after {
  left: 6px;
  top: 4px;
  width: 5px;
  height: 9px;
  border: solid #fff;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}
label.custom-radio .radio:after {
  left: 4px;
  top: 4px;
  width: 10px;
  height: 10px;
  border: 5px solid #fff;
  transform: rotate(90deg);
  display: block;
  border-radius: 4px;
}
