.main-navigation > nav > .main-menu > .menu-item:last-child {
  margin-right: 0;
  padding-right: 0;
}
.main-navigation > nav > .main-menu > .menu-item > a:after {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 2.5em;
  bottom: 0;
  width: 20%;
  height: 2px;
  background-color: #c9a050;
  content: "";
  z-index: 1;
  opacity: 0;
  transition: all 0.3s;
}
.main-navigation > nav > .main-menu > .menu-item:hover > a,
.main-navigation > nav > .main-menu > .menu-item.active > a {
  color: #c9a050;
}
.main-navigation > nav > .main-menu > .menu-item.active > a:after,
.main-navigation > nav > .main-menu > .menu-item:hover > a:after {
  opacity: 1;
  width: calc(100%);
}
.menu-item-has-children > a > .arrow:after {
  display: inline-block;
  margin-left: 0.55em;
  content: "\e688";
  font-family: "Pe-icon-7-stroke";
  font-weight: 900;
}
.menu-item-has-children > .sub-menu {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  transform: translateY(-10px);
  border-bottom: solid 2px #c9a050;
  box-shadow: 0 2px 2px 1px rgba(0, 0, 0, 0.15);
  background: #fff;
  visibility: hidden;
  opacity: 0;
  transition: 0.3s;
  width: 220px;
}
.menu-item-has-children:hover > .sub-menu {
  border-top: 1px solid rgba(67, 41, 163, 0.2);
  opacity: 1;
  visibility: visible;
  z-index: 999;
  transform: translateY(0px);
  transition: 0.3s;
}
.menu-item-has-children.nav-postion:hover > .sub-menu {
  top: 0;
  transform: translateY(-100%);
}
.menu-item-has-children > .sub-menu > li.menu-item {
  padding: 0 10px;
}
.menu-item-has-children.nav-postion
  > .sub-menu
  > li.menu-item-has-children
  > .sub-menu {
  top: inherit;
}
.menu-item-has-children > .sub-menu > li.menu-item > a {
  display: block;
  padding: 10px;
  font-size: 15px;
  font-weight: 400;
  border-bottom: 1px solid rgba(67, 41, 163, 0.2);
  transition: 0.5s;
  white-space: nowrap;
  color: #000000de;
}
