  padding-left: 25px;
  transition: 0.5s;
  color: #c9a050;
}
.menu-item-has-children > .sub-menu > li.menu-item-has-children > a > .arrow {
  position: absolute;
  right: 10px;
  transform: rotate(-90deg);
}
.menu-item-has-children
  > .sub-menu
  > li.menu-item-has-children
  > a
  > .arrow:after {
  display: inline-block;
  margin-left: 0;
  content: "\e688";
  font-family: "Pe-icon-7-stroke";
  font-weight: 900;
}
