.menu-item-has-megamenu > a > .arrow:after {
  display: inline-block;
  margin-left: 0.55em;
  content: "\e688";
  font-family: "Pe-icon-7-stroke";
  font-weight: 900;
}
.menu-item-has-megamenu > .megamenu {
  position: absolute;
  top: 100%;
  left: 15px;
  right: 0;
  transform: translateY(-10px);
  border-bottom: solid 2px #c9a050;
  box-shadow: 0 2px 2px 1px rgba(0, 0, 0, 0.15);
  background: #fff;
  visibility: hidden;
  opacity: 0;
  transition: 0.3s;
  width: calc(100% - 30px);
  background: #13130d;
  padding: 20px;
}
.menu-item-has-megamenu:hover > .megamenu {
  border-top: 1px solid rgba(67, 41, 163, 0.2);
  opacity: 1;
  visibility: visible;
  z-index: 999;
  transform: translateY(0px);
  transition: 0.3s;
}
.menu-item-has-megamenu > .megamenu .megamenu-nav .menu-item {
  padding: 0 10px;
}
.menu-item-has-megamenu > .megamenu .megamenu-nav .menu-item a {
  display: block;
  padding: 10px;
  font-size: 15px;
  font-weight: 100;
  border-bottom: 1px solid rgba(255, 255, 255, 0.29);
  transition: 0.5s;
  white-space: nowrap;
  color: #ffffff;
  background-color: transparent;
}
.menu-item-has-megamenu > .megamenu .megamenu-nav .menu-item:hover a {
  padding-left: 25px;
  transition: 0.5s;
  color: #c9a050;
}
.menu-item-has-megamenu > .megamenu .layout-img {
  transition: 0.5s all;
}
