10.Final Order
12.About Us
*/
/*======================
1.General Code
========================*/

html {
  overflow-x: hidden;
}
button:focus,
*:focus {
  outline: none;
}
body {
  font-family: "Muli";
  margin: 0px;
  padding: 0px;
  font-size: 13px;
  line-height: 1.8;
  font-weight: 400;
  color: #000000;
  background: #ffffff;
  transition: -webkit-transform ease-in 0.4s;
  transition: transform ease-in 0.4s;
  transition: transform ease-in 0.4s, -webkit-transform ease-in 0.4s;
  overflow: hidden;
}
button {
  background: transparent;
  border: none;
  padding: 0;
}
label {
  line-height: normal;
}
.align-item-center {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;
}
.p-relative {
  position: relative;
}
.after-none:after,
.none {
  display: none;
}
.custom-container {
  padding-right: 8%;
  padding-left: 8%;
}
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
}
.overlay-3 {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
