/*Navigation*/

@-webkit-keyframes fadeHeaderInDown {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
  }
  100% {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@keyframes fadeHeaderInDown {
  0% {
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
            transform: translate3d(0, -100%, 0);
  }
  100% {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}
.header.header-2.sticky,
.header.sticky {
  -webkit-animation-name: fadeHeaderInDown;
          animation-name: fadeHeaderInDown;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 22;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  background-color: #0a3161;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.01), 0 1px 8px 0 rgba(0, 0, 0, 0.25);
}
.header {
  position: fixed;
  left: 0;
  z-index: 10;
  display: flex;
  align-self: center;
  align-items: center;
  width: 100%;
  /* height: 60px; */
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  transition: all 0.2s ease;

  background-color: transparent;
}
.header.header-2 {
  background-color: #13130d82;
}
.header.not-fixed {
  position: relative;
}
.header .navigation {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header .navigation .logo {
  width: 10%;
  margin: 10px 0;
  z-index: 10;
}
.main-navigation > nav > .main-menu {
  display: flex;
  align-items: center;
}
.main-navigation > nav > .main-menu > .menu-item {
  margin: 0 10px;
  padding: 15px 0;
}
.main-navigation > nav > .main-menu > .menu-item > a {
  font-weight: 400;
  font-size: 14px;
  padding: 0px 10px;
  text-transform: uppercase;
}
header.sticky .main-navigation > nav > .main-menu > .menu-item > a {
  color: #fff;
}
.main-navigation > nav > .main-menu > .menu-item:last-child > a,
.main-navigation > nav > .main-menu > .menu-item:last-child {
  margin-right: 0;
  padding-right: 0;
}
.main-navigation > nav > .main-menu > .menu-item > a:after {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 2.5em;
  bottom: 0;
  width: 20%;
  height: 2px;
  background-color: #c9a050;
  content: "";
  z-index: 1;
  opacity: 0;
  transition: all 0.3s;
}
.main-navigation > nav > .main-menu > .menu-item:hover > a,
.main-navigation > nav > .main-menu > .menu-item.active > a {
  color: #c9a050;
}
.main-navigation > nav > .main-menu > .menu-item.active > a:after,
.main-navigation > nav > .main-menu > .menu-item:hover > a:after {
  opacity: 1;
  width: calc(100%);
}
.menu-item-has-children > a > .arrow:after {
  display: inline-block;
  margin-left: 0.55em;
  content: "\e688";
  font-family: "Pe-icon-7-stroke";
  font-weight: 900;
}
.menu-item-has-children > .sub-menu {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
  border-bottom: solid 2px #c9a050;
  box-shadow: 0 2px 2px 1px rgba(0, 0, 0, 0.15);
  background: #fff;
  visibility: hidden;
  opacity: 0;
  transition: 0.3s;
  width: 220px;
}
.menu-item-has-children:hover > .sub-menu {
  border-top: 1px solid rgba(67, 41, 163, 0.2);
  opacity: 1;
  visibility: visible;
  z-index: 999;
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  transition: 0.3s;
}
.menu-item-has-children.nav-postion:hover > .sub-menu {
  top: 0;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}
.menu-item-has-children > .sub-menu > li.menu-item {
  padding: 0 10px;
}
.menu-item-has-children.nav-postion
  > .sub-menu
  > li.menu-item-has-children
  > .sub-menu {
  top: inherit;
}
.menu-item-has-children > .sub-menu > li.menu-item > a {
  display: block;
  padding: 10px;
  font-size: 15px;
  font-weight: 400;
  border-bottom: 1px solid rgba(67, 41, 163, 0.2);
  transition: 0.5s;
  white-space: nowrap;
  color: #000000de;
}
.menu-item-has-children > .sub-menu > li.menu-item:last-child > a {
  border: none;
}
.menu-item-has-children > .sub-menu > li.menu-item:hover > a {
  padding-left: 25px;
  transition: 0.5s;
  color: #c9a050;
}
.menu-item-has-children > .sub-menu > li.menu-item-has-children > a > .arrow {
  position: absolute;
  right: 10px;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}
.menu-item-has-children
  > .sub-menu
  > li.menu-item-has-children
  > a
  > .arrow:after {
  display: inline-block;
  margin-left: 0;
  content: "\e688";
  font-family: "Pe-icon-7-stroke";
  font-weight: 900;
}
.menu-item-has-children > .sub-menu > li.menu-item-has-children > .sub-menu {
  left: 100%;
  top: -1px;
  margin-top: 0;
}
