  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  transition: 0.3s;
  position: relative;
  top: 7.4px;
  background-color: #c9a050;
}
.header
  .navigation
  .right-side-navigation
  .hamburger-menu
  .menu-btn.active
  span:nth-child(2) {
  display: none;
}
.header
  .navigation
  .right-side-navigation
  .hamburger-menu
  .menu-btn.active
  span:last-child {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  transition: 0.3s;
  background-color: #c9a050;
}
